<template>
  <div class="cart">
    <div class="cart-wrap">
      <div class="cart__block">
        <div class="cart__title">
          <h1>Корзина</h1>
        </div>
        <div class="cart__close" @click="closecart(), activeScrolling()">
          <img src="../../assets/img/cross2.png" alt="Товар" />
        </div>
      </div>

      <CartItem
        v-for="(item, index) in cart_data"
        :key="item.article"
        v-bind:cart_item_data="item"
        @deleteCart="deleteCart(index)"
      />
      <div class="cart__price">
        <p>
          Общая стоимость: <span> {{ cartTotalCost }}</span> ₽
        </p>
      </div>

      <router-link :to="{ name: name }">
        <button
          type="submit"
          class="cart__btn"
          @click="closecart(), activeScrolling()"
        >
          Оформление заказа
        </button>
      </router-link>
    </div>
    <div class="mini-block-footer">
      <Footer />
    </div>
  </div>
</template>

<script>
import CartItem from "@/components/cart/cart-item.vue";
import Footer from "@/components/footer";
import { mapActions } from "vuex";
export default {
  name: "cart",
  data() {
    return {
      name: "order",
    };
  },
  props: {
    cart_data: {
      type: Array,
      default() {
        return [];
      },
    },
    scrolled: {
      type: Boolean,
      default() {
        return "";
      },
    },
  },
  methods: {
    ...mapActions(["DELETE_FROM_CART"]),
    deleteCart(index) {
      this.$emit("deleteCart");
      // console.log(index);
      // this.DELETE_FROM_CART(index);
    },
    closecart() {
      this.$emit("closecart");
    },
    activeScrolling() {
      this.$emit("activeScrolling");
    },
    //  ShowCart() {
    //    if (this.show) {
    //      this.show = false;
    //    } else this.show = true;
    //  },
  },
  computed: {
    cartTotalCost() {
      let result = [];
      const pricerow = 1320;
      for (let item of this.cart_data) {
        result.push(item.price * item.section + pricerow * item.row);
      }
      result = result.reduce(function (sum, el) {
        return sum + el;
      });
      return result;
    },
  },

  components: {
    CartItem,
    Footer,
  },
};
</script>

