<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "products",
  data() {
    return {
      cart: [],
      section: "",
      row: "",
      selectedColorCross: [],
      selectedColorBarrels: [],
      selectedColorRow: [],
      selectedRow: "",
    };
  },
  props: {
    product_data: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  methods: {
    ...mapActions(["ADD_TO_CART"]),
    minus() {
      console.log("минус сработал");
      let input = document.getElementById("input");
      let total = document.getElementById("input").previousElementSibling;
      if (input.value > 1) {
        input.value--;
        this.section = input.value;
        console.log(this.section);
        return this.section;
      }
    },
    plus() {
      console.log("плюс сработал");
      let input = document.getElementById("input");
      let total = document.getElementById("input").nextElementSibling;
      input.value++;
      this.section = input.value;
      console.log(this.section);
      return this.section;
    },
    minusrow() {
      let input = document.getElementById("inputrow");
      let total = document.getElementById("inputrow").previousElementSibling;
      if (input.value > 1) {
        input.value--;
        this.row = input.value;
        return this.row;
      }
    },
    plusrow() {
      let input = document.getElementById("inputrow");
      let total = document.getElementById("inputrow").nextElementSibling;
      input.value++;
      this.row = input.value;
      return this.row;
    },
    addtoCart(product_data) {
      // this.BEFORECART.splice(product, 1);
      this.BEFORECART.push({
        name: product_data.name,
        price: product_data.price,
        ColorCross: this.selectedColorCross,
        ColorBarrels: this.selectedColorBarrels,
        ColorRow: this.selectedColorRow,
        selectedRow: this.selectedRow,
        section: this.section,
        row: this.row,
      }),
        localStorage.setItem("beforecart", JSON.stringify(this.BEFORECART));
      this.ADD_TO_CART(this.BEFORECART);
    },
    scrollToEnd: function () {
      var container = this.$el.querySelector("#header");
      if (window.innerWidth < 800) {
        let slide = document.getElementById("app");

        let top = window.scrollY + slide.getBoundingClientRect().y;

        window.scrollTo({
          top,
          behavior: "smooth",
        });
      }
    },
    // addtoCart(product_data) {
    //   // this.cart.splice(product, 1);
    //   this.CART.push({
    //     name: product_data.name,
    //     price: product_data.price,
    //     ColorCross: this.selectedColorCross,
    //     ColorBarrels: this.selectedColorBarrels,
    //     ColorRow: this.selectedColorRow,
    //     selectedRow: this.selectedRow,
    //     section: this.section,
    //     row: this.row,
    //   }),
    //     this.ADD_TO_CART(this.CART);
    // },
  },
  computed: {
    ...mapGetters(["BEFORECART"]),
    ...mapGetters(["CART"]),

    styleObjectCross() {
      return {
        backgroundColor: this.product_data.color,
      };
    },
    styleObjectBarrels() {
      return {
        backgroundColor: this.product_data.formBarrels,
      };
    },
    styleObjectRow() {
      return {
        backgroundColor: this.product_data.formRow,
      };
    },
  },
};
</script>

